<template>
    <div class="like">
      <ul>
        <li class="item" v-for="(item,index) in like" :key="index" @click="toessay(item.aid)">
          <!--<div class="head">-->
            <!--<img :src="item.header" :alt="item.name">-->
          <!--</div>-->
          <div class="info">
            <div class="tips">{{item.msg}}</div>
            <!--<div class="tips">{{item.name}}给你的回答点了赞</div>-->
            <!--<div class="title">{{item.title}}</div>-->
            <!--<div class="date">{{item.date}}</div>-->
          </div>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "like",
        data() {
            return {

            }
        },
      props:{
        like:Array
      },
        mounted() {

        },
        methods: {
          //  跳往页面详情
          toessay(aid) {
            this.$router.push({path: '/essay',query:{aid}})
          },

        }
    }
</script>

<style scoped lang="scss">
.like{
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
  ul {
    width: 100%;
    box-sizing: border-box;
    .item {
      padding: 20px 0;
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      border-bottom: 1px solid #eee;
      .head {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 20px;

        > img {
          width: 100%;
          height: 100%;
        }
      }

      .info {
        width: 93%;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        .tips {
          font-size:14px;
          font-weight:400;
          color:rgba(51,51,51,1);
          line-height:18px;
        }

        .title {
          font-size:14px;
          font-weight:bold;
          color:rgba(51,51,51,1);
          line-height:25px;
        }

        .date {
          font-size:12px;
          font-weight:400;
          color:rgba(153,153,153,1);
          line-height:18px;
        }
      }
    }
  }
}
</style>



