<template>
  <div class="fans">
    <ul>
      <li v-for="(item,index) in attention" :key="index">
        <div class="left">
          <div class="img"><img :src="item.head" alt=""></div>
          <div class="info">
            <div class="title">{{item.msg}}</div>
            <!--<div class="dec">{{item.summary}}</div>-->
          </div>
        </div>
     <!--   <div class="right">
          <el-button v-if="item.is_attention==0" type="danger" @click="toattention(item.fuid,index)" size="small">关注
          </el-button>
          <el-button  v-else size="small" @click="toattention(item.fuid,index)">已关注</el-button>
        </div>-->
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "atten",
    props: {
      attention: Array
    },
    data(){
      return{
        // userData:[]
      }
    },
    mounted() {
      // this.init()
    },
    methods: {
      //页面初始化
   /*   init() {
        this.userData=this.users.slice();
        setTimeout(() => {
          this.userData.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.fuid + "&amp;size=big"
          });
        }, 500)
      },*/
      //关注
      toattention(uid, index) {
        this.$post('index/data/attention_index', {uid}).then(res => {
          if (this.userData[index].is_attention === 1) {
            this.userData[index].is_attention = 0
          } else {
            this.userData[index].is_attention = 1
          }
         this.userData=this.userData.slice();
          if (res.data.result == 200) {
            this.$message.success(res.data.msg);

          } else {
            this.$message.warning(res.data.msg);
          }
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .fans {
    width: 100%;

    ul {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 20px;
      box-sizing: border-box;

      li {
        height: 82px;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid #eee;

        .left {
          display: flex;

          .img {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-left: 12px;
            margin-right: 20px;

            img {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              overflow: hidden;
            }
          }
        }

        .info {
          display: flex;
          flex-direction: column;
          height: 100%;

          .title {
            margin-top: 15px;
            font-size: 16px;
            width: 400px;
            font-weight: bold;
            color: rgba(51, 51, 51, 1);
            line-height: 25px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }

          .dec {
            width: 500px;
            font-size: 14px;
            font-weight: 400;
            color: rgba(153, 153, 153, 1);
            line-height: 25px;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }

        .right {
          display: flex;
          align-items: center;
        }

      }
    }
  }
</style>



