<template>
  <div class="attention">
    <div class="tab">
      <ul>
        <li @click="toactive('collects')" :class="currentView==='collects'?'active':''" class="question">关注问题</li>
        <li @click="toactive('fans')" :class="currentView==='fans'?'active':''" class="followee">关注的人</li>
      </ul>
    </div>
    <div class="contain">
      <component :users="attUser" :collect="collect" :is="currentView"></component>
    </div>
  </div>
</template>

<script>
  import fans from './fans'
  import collects from "./collects";

  export default {
    name: "attention",
    data() {
      return {
        currentView: "collects",
        attUser: [],
        collect: []
      }
    },
    components: {
      fans,
      collects
    },
    mounted() {
      this.getUser();
      this.getCollect()
    },
    methods: {
      toactive(el) {
        this.currentView = el;
      },
      //关注的人
      getUser() {
        let uid = this.$route.query.otheruid;
        this.$post('index/data/attention', {uid}).then(res => {
          this.attUser = res.data;
          this.attUser.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.fuid + "&amp;size=big"
          });
        }).catch(err => {
          console.log(err);
        })
      },
      //关注问题
      getCollect() {
        let uid = this.$route.query.otheruid;
        this.$post('index/data/personal_collect', {uid}).then(res => {
          this.collect = res.data.data;
          // this.getCollect.map((value, index, array) => {
          //   value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.fuid + "&amp;size=big"
          // });
        }).catch(err => {
          console.log(err);
        })
      },

    }
  }
</script>

<style scoped lang="scss">
  .attention {
    width: 100%;

    .tab {
      width: 100%;
      height: 40px;
      font-size: 16px;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      line-height: 40px;
      padding: 0 32px;
      box-sizing: border-box;

      ul {
        display: flex;

        > li {
          cursor: pointer;
          margin-right: 50px;
        }

        .active {
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          position: relative;

          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 6px;
            background: rgba(0, 150, 255, 1);
            border-radius: 3px;
          }
        }

      }
    }
  }
</style>



