<template>
  <div class="doubt">
    <ul>
      <li  @click="toessay(item.aid)" v-for="(item,index) in doubt" :key="index">
        <div class="title">{{item.title}}</div>
        <div class="info">
          <div class="ans">{{item.commentnum}}回答</div>
          <div class="col">{{item.favtimes}}收藏</div>
        </div>
        <div class="toans"><img src="@/images/write.png" alt=""> 回答</div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "doubt",
    props: {
      doubt: Array,
    },
    methods:{
      //  跳往页面详情
      toessay(aid) {
        this.$router.push({path: '/essay',query:{aid}})
      },
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .doubt {
    width: 100%;

    ul {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 20px;
      box-sizing: border-box;

      li {
        cursor: pointer;
        height: 117px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        border-bottom: 1px solid #eee;

        &:hover .title {
          color: $main;
        }

        .title {
          height: 28px;
          font-size: 20px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 28px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }

        .info {
          font-size: 12px;
          font-weight: 400;
          color: rgba(153, 153, 153, 1);
          line-height: 18px;
          display: flex;

          .col {
            margin-left: 15px;
          }
        }

        .toans {
          font-size: 14px;
          font-weight: 400;
          color: rgba(0, 150, 255, 1);
          line-height: 20px;

          img {
            width: 18px;
            height: 16px;
            vertical-align: middle;
            margin-right: 5px;
          }
        }
      }
    }
  }
</style>



