<template>
  <div class="user" v-wechat-title="$route.meta.title">
    <div class="operation">
      <div class="left">
        <div class="tab-head">
          <ul>
            <li :class="selected==item.name?'selected':''" v-for="(item,index) in tablist" :key="index"
                @click="toselect(item.name)">{{item.title}} <span>{{item.num}}</span></li>
          </ul>
        </div>
        <component :article="answers"   v-on:islike="islike" v-on:isrubbish="isrubbish" :users="fans" :command="command" :collect.sync="collect" :draft.sync="draft"
                   :doubt.sync="doubt"
                   :is="currentView" v-on:delect="successDel"></component>
        <div class="empty">
          没有更多数据了
        </div>
      </div>

      <div class="right">
        <div class="userinfo">
          <div class="header">
            <img :src="userInfo.head" alt="">
          </div>
          <div class="name">{{userInfo.username}}</div>
          <div class="dec">{{userInfo.bio}}</div>
          <div class="edit" v-if="other">
            <el-button v-if="userInfo.is_attention==1" @click="toattention(otheruid)">取消关注</el-button>
            <el-button  v-else type="danger" @click="toattention(otheruid)">关注</el-button>
          </div>
          <div class="edit" v-else>
            <el-button type="primary" class="editbtn" @click="$router.push('editUser')">编辑个人资料</el-button>
          </div>

        </div>
        <keep-alive>
          <fansnumber :otheruid="otheruid"></fansnumber>
        </keep-alive>
      </div>
    </div>
  </div>
</template>

<script>
  import doubt from "./doubt";
  import answ from "./answ";
  import draft from "./draft";
  import fans from "./fans";
  import attention from "./attention";
  import message from "./message";
  import collects from "./collects";
  import fansnumber from "./fansnumber";

  export default {
    name: "user",
    components: {
      answ,
      doubt,
      draft,
      fans,
      attention,
      message,
      collects,
      fansnumber
    },
    data() {
      return {
        uid: '',
        tablist: [],
        selected: 'dynamic',
        userInfo: {},
        currentView: 'answ',
        collect: [],
        doubt: [],
        draft: [],
        command: 'inform',
        answers: [],
        fans: [],
        otheruid: '',
        other: false

      };
    },
    watch: {
      otheruid(val) {
        let uid = this.$store.state.userInfo.uid;
        if (val == uid) {
          this.other = false
        } else {
          this.other = true
        }
      },

      $route() {
        this.init()
      },
    },
    create() {
    },
    mounted() {
      // this.userInfo = this.$store.state.userInfo;
      this.init();
      this.getdata();
      this.getnav();
    },
    methods: {
      //页面初始化
      init() {
        let type = this.$route.query.type;
        if (type) {
          this.toselect(type);
        }
        if (this.$route.query.message) {
          // this.selected = "message";
          // this.currentView = 'message';
          // this.otheruid = this.$route.query.otheruid;
          // this.command = this.$route.query.message;
        }
      },
      //点赞成功
      islike(data){
        if (data == true) {
          this.getAnswers();
        }
      },
      //踩成功
      isrubbish(data){
        if (data == true) {
          this.getAnswers();
        }
      },
      //草稿删除成功
      successDel(data) {
        if (data == "true") {
          this.getnav();
          this.getdraft();
        }
      },
      //获取用户基本信息
      getuser() {
        this.$post('index/data/member_info', {uid: this.otheruid}).then(res => {
          this.userInfo = res.data;
          this.userInfo.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + this.userInfo.uid + "&amp;size=big"
        }).catch(err => {
          console.log(err);
        })
      },
      //获取导航数据
      getnav() {
        setTimeout(() => {
          this.$post('index/data/card_nav', {uid: this.otheruid}).then(res => {
            this.tablist = res.data;
          }).catch(err => {
            console.log(err);
          })
        }, 500)
      },
      //关注
      toattention(uid) {
        this.$post('index/data/attention_index', {uid}).then(res => {
          if (res.data.result == 200) {
            this.getuser();
            this.$message.success(res.data.msg)
          } else {
            this.$message.warning(res.data.msg)
          }
        }).catch(err => {
          console.log(err);
        })
      },
      getdata() {
        if (this.$route.query.otheruid) {
          this.otheruid = this.$route.query.otheruid;
          this.uid = this.otheruid;
        } else {
          this.uid = this.$store.state.userInfo.uid;
          this.otheruid = this.uid;
        }
        setTimeout(() => {
          this.getuser();
          this.getcollect();
          this.getdoubt();
          this.getdraft();
          this.getAnswers();
          this.getfans();
        }, 500)
      },
      //获取回答数据
      getAnswers() {
        this.$post('index/data/personal_answer', {uid: this.otheruid}).then(res => {
          this.answers = res.data.data;
          this.answers.map((value, index, array) => {
            value.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + value.uid + "&amp;size=big"
          });
          this.answers = this.answers.slice();
        }).catch(err => {
          console.log(err);
        })
      },
      //收藏数据
      getcollect() {
        this.$post('index/data/personal_collect', {uid: this.otheruid}).then((res) => {
          this.collect = res.data.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //提问数据
      getdoubt() {
        this.$post('index/data/personal_question', {uid: this.otheruid}).then((res) => {
          this.doubt = res.data.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //获取粉丝数据
      getfans() {
        this.$post('index/data/fuser', {uid: this.otheruid}).then(res => {
          this.fans = res.data;
        }).catch(err => {
          console.log(err);
        })
      },
      //草稿数据
      getdraft() {
        this.$post('index/data/personal_draft', {uid: this.otheruid}).then((res) => {
          this.draft = res.data.data;
          this.draft.map((val, index, arr) => {
            val.head = this.$httpUrl + "/uc_server/avatar.php?uid=" + val.uid + "&amp;size=big";
          });
          this.draft.slice();
        }).catch(err => {
          console.log(err);
        })
      },
      // tab栏切换
      toselect(name) {
        this.selected = name;
        let otheruid = this.$route.query.otheruid;
        let message = this.$route.query.message;
        if (message) {
          this.$router.push({path: '/user', query: {otheruid, type: name, message}});
        } else {
          this.$router.push({path: '/user', query: {otheruid, type: name}});
        }
        switch (name) {
          case "fans":
            this.currentView = "fans";
            break;
          case 'dynamic':
            this.currentView = "answ";
            break;
          case "answer":
            this.currentView = "answ";
            break;
          case "doubt":
            this.currentView = "doubt";
            break;
          case "attention":
            this.currentView = "attention";
            break;
          case "draft":
            this.currentView = "draft";
            break;
          // case "collect":
          //   this.currentView = "collects";
            break;
          case "message":
            this.currentView = "message";
            break;
        }
      },

    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  /deep/ .editbtn{
    margin: 0 0 10px;
  }
  .operation {
    width: 1180px;
    margin: 36px auto 0;
    display: flex;
    justify-content: space-between;

    > .left {
      width: 790px;
      box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
      margin-bottom: 10px;

      .tab-head {
        height: 50px;

        > ul {
          padding: 0;
          list-style: none;
          display: flex;
          justify-content: flex-start;
          margin: 0 20px;
          border-bottom: 1px solid rgba(221, 221, 221, 1);

          li {
            font-weight: bold;
            font-size: 16px;
            color: rgba(51, 51, 51, 1);
            line-height: 48px;
            margin-right: 48px;
            cursor: pointer;
            moz-user-select: -moz-none;
            -moz-user-select: none;
            -o-user-select: none;
            -khtml-user-select: none;
            -webkit-user-select: none;
            -ms-user-select: none;
            user-select: none;

            span {
              font-size: 16px;
              font-weight: bold;
              color: #a5a5a5;
              line-height: 34px;
            }
          }


          .selected {
            color: $main;
            span{
              color: $main;
            }
          }
        }
      }

      .tab-content {

        > div {
          display: none;
        }

        .show {
          display: flex;
        }

      }

      .empty {
        width: 100%;
        text-align: center;
        padding: 40px 0;
        color: #8c939d;
      }
    }

    > .right {
      width: 350px;

      .userinfo {
        text-align: center;
        width: 100%;
        height: 301px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        box-sizing: border-box;

        .header {
          width: 82px;
          height: 82px;
          border-radius: 50%;
          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
          }
        }

        .name {
          width: 108px;
          font-size: 26px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 26px;
        }

        .dec {
          width: 203px;
          height: 25px;
          font-size: 16px;
          font-weight: 400;
          color: rgba(102, 102, 102, 1);
          line-height: 16px;
          display: block;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }

        .edit {
        }
      }

      .person {
        margin-top: 14px;
        background: rgba(255, 255, 255, 1);
        box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.22);
        border-radius: 6px;
        height: 168px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        box-sizing: border-box;

        .lis {
          font-size: 16px;
          font-weight: 400;
          color: rgba(51, 51, 51, 1);
          line-height: 25px;
          box-sizing: border-box;

          > img {
            width: 16px;
            height: 16px;
          }

          span {
            margin-left: 16px;
            font-size: 16px;
            font-weight: 400;
            color: rgba(51, 51, 51, 1);
            line-height: 30px;
          }
        }
      }
    }
  }


</style>



