<template>
  <div class="message">
    <div class="tab">
      <ul>
        <li v-for="(item,index) in tablist" :key="index" @click="toactive(item.name)"
            :class="active===item.name?'active':''">{{item.title}}
        </li>
      </ul>
    </div>
    <div class="contain">
      <component :invite="inviteData" :inform="informData" :attention="attentionData" :like="likedata"
                 :comment="commentData" :is="currentView"></component>
    </div>
  </div>
</template>

<script>
  import inform from "./inform"
  import invite from "./invite"
  import like from "./like"
  import comment from "./comment"
  import atten from "./atten"

  export default {
    name: "message",
    data() {
      return {
        tablist: [
          {"name": "inform", title: '通知'},
          {"name": "invite", title: '邀请'},
          {"name": "like", title: '点赞'},
          {"name": "comment", title: '评论'},
          {"name": "atten", title: '关注'},
        ],
        active: 'inform',
        currentView: "inform",
        informData: [],
        likedata: [],
        attentionData: [],
        commentData: [],
        inviteData: [],
      }
    },
    props: {
      command: String
    },
    components: {
      inform,
      invite,
      like,
      comment,
      atten
    },
    watch: {
      $route(){
        let name=this.$route.query.message;
        if(name){
          this.toactive(name)
        }
      },
      command(val) {
        this.active = val;
        this.currentView = val;
      }
    },
    mounted() {
      this.active = this.command;
      this.currentView = this.command;
      let name=this.$route.query.message;
      this.toactive(name);
      this.getInform();
    },
    methods: {
      toactive(name) {
        this.active = name;
        let uid=this.$store.state.userInfo.uid;
        this.$router.push({path: '/user', query: {otheruid:uid,type:'message', message: name}});
        switch (name) {
          // case "atten":
          //   this.currentView = "fans";
          //   break;
          default:
            this.currentView = name;
            break;
        }
      },
      //获取通知
      getInform() {
        let uid = this.$store.state.userInfo.uid;
        this.$post('index/data/message', {uid}).then(res => {
          this.informData = res.data;
          this.informData.map((value, index, array) => {
            if (value.type === 1) {
              this.likedata.push(value)
            } else if (value.type === 3) {
              this.commentData.push(value)
            } else if (value.type === 5) {
              this.attentionData.push(value)
            } else if (value.type === 6) {
              this.inviteData.push(value)
            }
          })
        }).catch(err => {
          console.log(err);
        })
      },
    }
  }
</script>

<style scoped lang="scss">
  .message {
    width: 100%;

    .tab {
      width: 100%;
      height: 40px;
      font-size: 16px;
      font-weight: 400;
      color: rgba(51, 51, 51, 1);
      line-height: 40px;
      padding: 0 32px;
      box-sizing: border-box;

      ul {
        display: flex;

        > li {
          cursor: pointer;
          margin-right: 50px;
        }

        .active {
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          position: relative;

          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 6px;
            background: rgba(0, 150, 255, 1);
            border-radius: 3px;
          }
        }

      }
    }
  }
</style>



