<template>
  <div class="draft">
    <ul>
      <li v-for="(item,index) in draft" :key="index">
        <div @click="toessay(item.aid)" class="title">{{item.title}}</div>
        <div class="user" @click="touser(item.uid)">
          <div class="img"><img :src="item.head" alt=""></div>
          {{item.username}}
        </div>
        <div class="word" v-html="item.content"></div>
        <div class="opt">
          <div class="continue" @click="toessay(item.aid)"><img src="@/images/write.png" alt="">继续回答</div>
          <!--<div class="delete" @click="todelete(index,item.id,item.uid,item.title)"><img src="@/images/write.png" alt="">删除-->
          <!--</div>-->
          <div class="time">保存于 {{item.dateline|dayFmt}}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "draft",
    props: {
      draft: Array,
    },
    data() {
      return {

      }
    },
    mounted() {
      // this.getData()
    },
    methods: {
      //用户详情
      touser(uid) {
        this.$router.push({name: 'user', query: {otheruid: uid}})
      },
      //  跳往页面详情
      toessay(aid) {
        this.$router.push({path: '/essay', query: {aid}})
      },
      //删除
      todelete(index, aid, uid, content) {
        this.$post('index/data/draft_edit', {aid, uid, content}).then(res => {
          if (res.data.result == 200) {
            // this.drafts.map((val, i, arr) => {
            //   if (index == i) {
            //     this.drafts.splice(index, 1);
            //   }
            // });
            // this.drafts = this.drafts.slice();
            this.$emit('delect','true');
            this.$message.success(res.data.msg);
          } else {
            this.$message.warning(res.data.msg)
          }
        }).catch(err => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  $main: #409eff !default;
  .draft {
    width: 100%;

    ul {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 0 20px;
      box-sizing: border-box;

      li {
        display: flex;
        flex-direction: column;
        /*justify-content: space-around;*/
        border-bottom: 1px solid #eee;

        &:hover .title {
          color: $main;
        }

        .title {
          cursor: pointer;
          height: 28px;
          font-size: 20px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 28px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          padding-top: 10px;
        }

        .user {
          font-size: 12px;
          font-weight: bold;
          color: rgba(51, 51, 51, 1);
          line-height: 34px;
          display: flex;
          cursor: pointer;

          .img {
            width: 24px;
            height: 24px;
            overflow: hidden;
            border-radius: 50%;
            margin-right: 15px;
            vertical-align: middle;

            img {
              width: 100%;
              height: 100%;
            }
          }

        }

        .word {
          font-size: 14px;
          font-weight: 400;
          color: rgba(153, 153, 153, 1);
          line-height: 28px;
        }

        .opt {

          font-size: 14px;
          font-weight: 400;
          color: rgba(165, 165, 165, 1);
          line-height: 44px;
          display: flex;

          > div {
            margin-right: 47px;
          }


          .continue {
            font-size: 14px;
            font-weight: 400;
            color: rgba(0, 150, 255, 1);
            line-height: 44px;
            display: flex;
            align-items: center;
            cursor: pointer;

            img {
              width: 15px;
              height: 15px;
              vertical-align: middle;
              margin-right: 5px;
            }
          }

          .delete {
            cursor: pointer;

            img {
              width: 15px;
              height: 15px;
              vertical-align: middle;
              margin-right: 5px;
            }
          }

          .time {
          }
        }
      }
    }
  }
</style>



