<template>
  <div class="inform">
    <ul>
      <li class="info" v-for="(item,index) in inform" :key="index" @click="toessay(item.aid)">
        <div class="title" v-html="item.msg"></div>
        <!--<div class="title">你的提问收到了1个新回答,快去看看吧</div>-->
        <!--<div class="date">2019/03/05 10:34</div>-->
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name: "inform",
    data() {
      return {}
    },
    props:{
      inform:Array
    },
    mounted() {

    },
    methods: {
      //  跳往页面详情
      toessay(aid) {
        this.$router.push({path: '/essay',query:{aid}})
      },
    }
  }
</script>

<style scoped lang="scss">
  .inform {
    width: 100%;
    padding:0 20px;
    box-sizing: border-box;
    ul {
      width: 100%;
      display: flex;
      flex-direction: column;
      .info {
        cursor: pointer;
        height: 64px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-bottom: 1px solid #eee;
        .title {
          font-size:14px;
          font-weight:bold;
          color:rgba(51,51,51,1);
          line-height:24px;
        }

        .date {
          font-size:12px;
          font-weight:400;
          color:rgba(153,153,153,1);
          line-height:24px;
        }
      }
    }
  }
</style>



